-50%);\r\n        width: 25px;\r\n        height: 1px;\r\n        background-color: $color;\r\n\r\n        @include setTabletStyles {\r\n            width: 61px;\r\n            left: 50%;\r\n            bottom: -10px;\r\n            top: unset;\r\n            transform: translateX(-50%);\r\n        }\r\n    }\r\n}\r\n\r\n.home__heading {\r\n    margin-bottom: 10px;\r\n}\r\n\r\n.home__text__additional {\r\n    font-size: 18px;\r\n    margin-bottom: 40px;\r\n    color: $light-gray;\r\n\r\n    @include setTabletStyles {\r\n        margin-bottom: 20px;\r\n    }\r\n}\r\n\r\n.home__btn {\r\n    padding-left: 30px;\r\n    padding-right: 30px;\r\n    width: fit-content;\r\n    margin-bottom: 40px;\r\n\r\n    @include setTabletStyles {\r\n        width: 100%;\r\n        margin-top: 20px;\r\n        order: 2;\r\n    }\r\n}\r\n\r\n.home__items {\r\n    gap: 10px;\r\n}\r\n\r\n.home__item {\r\n    @extend %list-item;\r\n    font-weight: bold;\r\n    padding-left: 36px;\r\n}\r\n\r\n.home__gallery__links {\r\n    position: absolute;\r\n    z-index: 61;\r\n    top: 330px;\r\n    gap: 10px;\r\n    right: calc(-1 * var(--horizontalPadding));\r\n\r\n    @media (min-width: 1315px) {\r\n        right: -100px;\r\n    }\r\n\r\n    @extend %showOnlyFromTablet;\r\n\r\n}\r\n\r\n.home__gallery__link {\r\n    padding: 5px;\r\n    background: transparent;\r\n    border-radius: 28px;\r\n    text-decoration: none;\r\n    transition: $transitionTime-main;\r\n\r\n    &__iconWrap {\r\n        width: 60px;\r\n        height: 60px;\r\n        padding: 15px;\r\n        border-radius: 25px;\r\n        background: $white;\r\n    }\r\n\r\n    &__icon {\r\n        width: 100%;\r\n        height: 100%;\r\n        fill: $color;\r\n    }\r\n\r\n    &__info {\r\n        visibility: hidden;\r\n        color: $dark-gray;\r\n        width: 0;\r\n    }\r\n\r\n    &:hover {\r\n        background: $white;\r\n        opacity: 1;\r\n\r\n        & .home__gallery__link__icon {\r\n            fill: $white;\r\n        }\r\n\r\n        & .home__gallery__link__iconWrap {\r\n            background: $color;\r\n        }\r\n\r\n        & .home__gallery__link__info {\r\n            visibility: visible;\r\n            padding: 0 20px 0 36px;\r\n            width: auto;\r\n        }\r\n    }\r\n}\r\n\r\n/* ======================== COST ======================== */\r\n\r\n.cost__heading {\r\n    @extend %heading--normal;\r\n    text-align: center;\r\n}\r\n\r\n.cost__heading__hr {\r\n    width: 75px;\r\n    height: 2px;\r\n    background-color: $white;\r\n    margin: 10px auto;\r\n}\r\n\r\n.cost__subheading {\r\n    @extend %font18to16;\r\n    text-align: center;\r\n}\r\n\r\n.cost__content {\r\n    display: flex;\r\n    margin-top: 25px;\r\n    gap: 30px;\r\n\r\n    @media (max-width: 1074px) {\r\n        flex-direction: column;\r\n        margin-top: 72px;\r\n        gap: 20px;\r\n    }\r\n}\r\n\r\n.cost__info {\r\n    min-width: 300px;\r\n    border-radius: $border-radius__main;\r\n    position: relative;\r\n    text-align: center;\r\n    height: 685px;\r\n    min-height: 650px;\r\n    padding: 0 23px 30px;\r\n\r\n    @media (min-width: 1075px) {\r\n        @include setDeskWidth(4);\r\n        padding: 0 27px 40px;\r\n        height: auto;\r\n    }\r\n\r\n    @include setTabletStyles {}\r\n\r\n    &__imgWrap {\r\n        position: absolute;\r\n        top: -19px;\r\n\r\n        .cost__info__img {\r\n            object-fit: cover;\r\n\r\n            @media (max-width: 1074px) {\r\n                height: 338px;\r\n            }\r\n        }\r\n\r\n        &::after {\r\n            content: \"\";\r\n            position: absolute;\r\n            left: 0;\r\n            top: 0;\r\n            width: 100%;\r\n            height: 100%;\r\n            background: linear-gradient(0deg, white, transparent 61.57%);\r\n        }\r\n    }\r\n\r\n    &__name {\r\n        font-size: 22px;\r\n        z-index: 2;\r\n    }\r\n\r\n    &__position {\r\n        color: $light-gray;\r\n        font-size: 14px;\r\n        opacity: 0.5;\r\n        margin-bottom: 10px;\r\n    }\r\n\r\n    &__text {\r\n        @extend %font14to12;\r\n        margin-bottom: 30px;\r\n        position: relative;\r\n\r\n        @include setTabletStyles {\r\n            padding: 0 10px;\r\n            margin-bottom: 10px;\r\n        }\r\n\r\n        &::before,\r\n        &::after {\r\n            content: \"\";\r\n            position: absolute;\r\n            width: 32px;\r\n            height: 21px;\r\n            background-image: url(../img/icons/quotas.png);\r\n        }\r\n\r\n        &::before {\r\n            top: 0;\r\n            left: 0;\r\n            transform: translateY(-100%);\r\n        }\r\n\r\n        &::after {\r\n            bottom: 0;\r\n            right: 0;\r\n            transform: translateY(100%) rotate(180deg);\r\n\r\n            @include setMobileStyles {\r\n                transform: translate(50%, 80%) rotate(180deg);\r\n                ;\r\n            }\r\n        }\r\n    }\r\n\r\n    &__benefits {\r\n        gap: 13px;\r\n\r\n        .cost__info__benefit {\r\n            gap: 12px;\r\n            text-align: left;\r\n            margin-left: 20px;\r\n\r\n            @include setTabletStyles {\r\n                margin-left: 0;\r\n            }\r\n        }\r\n\r\n        .cost__info__benefitsHeading {\r\n            font-size: 16px;\r\n        }\r\n    }\r\n}\r\n\r\n/* ======================== BENEFITS ======================== */\r\n\r\n#benefits {\r\n    @extend %flex-adaptive;\r\n    align-items: center;\r\n    gap: 40px;\r\n\r\n    @include setTabletStyles {\r\n        gap: 20px;\r\n    }\r\n\r\n    .benefits__content {\r\n        @include setWidthAdaptive(8);\r\n        @extend %flex-adaptive;\r\n        flex: 1;\r\n        justify-content: space-between;\r\n        align-items: flex-end;\r\n        flex-wrap: wrap;\r\n        gap: 34px;\r\n\r\n        @include setTabletStyles {\r\n            gap: 30px;\r\n        }\r\n\r\n        .benefits__heading {\r\n            flex-basis: 100%;\r\n            @extend %heading--medium;\r\n        }\r\n\r\n        .benefits__text__heading {\r\n            @extend %subheading--normal;\r\n            padding-bottom: 20px;\r\n\r\n            @include setTabletStyles {\r\n                padding-bottom: 10px;\r\n                font-weight: bold;\r\n            }\r\n        }\r\n\r\n        .benefits__list__header {\r\n            @extend %subheading--normal;\r\n            padding-bottom: 32px;\r\n\r\n            @include setTabletStyles {\r\n                padding-bottom: 20px;\r\n            }\r\n        }\r\n\r\n        .benefits__text,\r\n        .benefits__list {\r\n            max-width: 300px;\r\n            min-width: 200px;\r\n            text-align: left;\r\n            flex: 1;\r\n\r\n            @include setTabletStyles {\r\n                width: 100%;\r\n                max-width: unset;\r\n            }\r\n        }\r\n\r\n        .benefits__text p {\r\n            padding-bottom: 20px;\r\n\r\n            &:last-child {\r\n                padding-bottom: 0;\r\n            }\r\n        }\r\n    }\r\n\r\n    .benefits__video {\r\n        flex: 1;\r\n        @extend .d-flex--center;\r\n        @extend %bordered;\r\n        @include setWidthAdaptiveWithExtend(4, 30px);\r\n        position: relative;\r\n        left: 15px;\r\n        max-width: 380px;\r\n        min-width: 316px;\r\n        aspect-ratio: 380/496;\r\n        background-color: transparentize($color, $amount: 0.6);\r\n        z-index: 3;\r\n        overflow: hidden;\r\n\r\n        @include setTabletStyles {\r\n            box-shadow: none;\r\n            border: none;\r\n            height: 400px;\r\n            left: 0;\r\n            max-width: unset;\r\n        }\r\n\r\n        &:before {\r\n            content: \"\";\r\n            position: absolute;\r\n            top: 50%;\r\n            left: 50%;\r\n            transform: translate(-50%, -50%);\r\n            width: 56px;\r\n            aspect-ratio: 1;\r\n            border: 2px solid $white;\r\n            border-radius: 50%;\r\n            background: url(../img/icons/play.png) no-repeat center;\r\n            z-index: 2;\r\n        }\r\n\r\n        &::after {\r\n            content: \"\";\r\n            position: absolute;\r\n            top: 0;\r\n            left: 0;\r\n            width: 100%;\r\n            height: 100%;\r\n            background-color: transparentize($color, $amount: 0.6);\r\n            border-radius: $border-radius__main;\r\n\r\n            @include setTabletStyles {\r\n                background-color: rgba(0, 0, 0, 0.5);\r\n            }\r\n        }\r\n\r\n        .video__poster {\r\n            position: absolute;\r\n            height: 100%;\r\n            width: 100%;\r\n            object-fit: cover;\r\n            border-radius: $border-radius__main;\r\n        }\r\n\r\n        #player {\r\n            border-radius: $border-radius__main;\r\n        }\r\n\r\n        &.playing {\r\n\r\n            &::before,\r\n            & .video__poster {\r\n                display: none;\r\n            }\r\n\r\n            &::after {\r\n                background-color: transparent;\r\n            }\r\n        }\r\n\r\n        &.paused {\r\n            &::before {\r\n                display: initial;\r\n            }\r\n\r\n            &::after {\r\n                background-color: initial;\r\n            }\r\n\r\n            & .video__poster {\r\n                display: none;\r\n            }\r\n        }\r\n    }\r\n}\r\n\r\n/* ======================== PROJECTS ======================== */\r\n\r\n\r\n.projects__content {\r\n    flex-direction: column;\r\n    flex-wrap: wrap-reverse;\r\n    max-height: 700px;\r\n    gap: 95px;\r\n\r\n    @include setTabletStyles {\r\n        max-height: unset;\r\n        gap: 20px;\r\n    }\r\n}\r\n\r\n.projects__heading {\r\n    @extend %heading--normal;\r\n    text-align: center;\r\n\r\n    &__hr {\r\n        width: 61px;\r\n        height: 2px;\r\n        background-color: $color;\r\n        margin: 10px auto 42px;\r\n\r\n        @include setTabletStyles {\r\n            margin-bottom: 20px;\r\n        }\r\n    }\r\n}\r\n\r\n.project__img {\r\n    width: 640px;\r\n    aspect-ratio: 640 / 422;\r\n    min-height: 1px;\r\n    flex: 0 0 auto;\r\n    @extend %bordered;\r\n    border-radius: 8.75% / 13.27%;\r\n    position: relative;\r\n\r\n    @include setTabletStyles {\r\n        width: calc(100vw - 9px);\r\n        min-width: 366px;\r\n        aspect-ratio: 366 / 271;\r\n        right: calc(var(--horizontalPadding)*-1 + 9px);\r\n    }\r\n\r\n    @include setMobileStyles {\r\n        border-width: 10px;\r\n    }\r\n\r\n    @media (max-width: 1440px) {\r\n        border-left: none;\r\n        border-radius: 0 8.75% 8.75% 0 / 13.27%;\r\n    }\r\n\r\n    & .gallery__item {\r\n        width: 100%;\r\n        height: 100%;\r\n        border-radius: inherit;\r\n\r\n        .project__image {\r\n            width: 100%;\r\n            height: 100%;\r\n            object-fit: cover;\r\n            border-radius: inherit;\r\n        }\r\n\r\n        .project__characters {\r\n            position: absolute;\r\n            bottom: 35px;\r\n            left: 50%;\r\n            transform: translateX(-50%);\r\n            z-index: 1;\r\n            gap: 8px;\r\n            font-size: 12px;\r\n\r\n            @media (min-width: 769px) and (max-width: 1100px) {\r\n                left: unset;\r\n                right: 165px;\r\n                transform: none;\r\n                flex-direction: column;\r\n                align-items: flex-start;\r\n            }\r\n\r\n            @include setTabletStyles {\r\n                bottom: calc(4.8% + 18px);\r\n                left: unset;\r\n                right: calc(5.5% + 36px + 10px);\r\n                transform: translateY(50%);\r\n            }\r\n\r\n            .character {\r\n                @extend .d-flex--center;\r\n                color: $white;\r\n                white-spa